//
export function FingerprintVerification() {
    // 
    return new Promise((resolve, reject) => {
        FingerprintAuth.isAvailable(resultSuccess => {
            if (resultSuccess.isAvailable) {
                var config = {
                    clientId: "com.github.topcoder520_debug", // android key store
                    usename: "myUsername", // 
                    password: "myPassword",// 
                    //token: undefined, // 
                    disableBackup: false, // 
                    maxAttempts: '5', // 55
                    locale: "zh_CN", // en_US
                    userAuthRequired: "false",
                    //encryptNoAuth: undefined,
                    dialogTitle: '', // 
                    dialogMessage: '', // 
                    dialogHint: '' // 
                };
                FingerprintAuth.encrypt(config, rs => {
                    resolve(rs);
                }, err => {
                    reject({code:3,msg:""});
                });
            } else {
                reject({code:2,msg:""});
            }
        }, resultError => {
            reject({code:1,msg:resultError});
        });
    });
}
